.header {
	background: url("../img/header-bg.png") no-repeat center center/cover;
	color: $white;
	text-align: center;
	padding-top: 85px;
	padding-bottom: 160px;

	.head {
		margin-bottom: 180px;

		.logo {
			font-family: 'Montserrat', sans-serif;
			font-size: 45px;
			text-transform: uppercase;
			font-weight: 600;
			color: $white;
			width: 150px;
			height: 150px;
			display: block;
			border-radius: 100px;
			padding-top: 40px;
			flex-shrink: 0;
			z-index: 100;
			position: relative;
			transition: all 1.1s ease;

			&__svg {
				height: 150px;
				width: 150px;
				position: absolute;
				top: -2px;
				left: -2px;

			}
			&__circle1,
			&__circle3 {
				stroke: $white;
				stroke-width: 3;
				stroke-dasharray: 500 60;
				z-index: 5;
				opacity: 1;
			}
			&__circle2,
			&__circle4 {
				stroke: $orange;
				stroke-width: 3;
				stroke-dasharray: 0 250;
				z-index: 6;
				transition: all 1.1s ease;
				stroke-dashoffset: 200px;
				opacity: 1;
			}
			&__circle3 {
				display: none;
				stroke-width: 2;
				stroke-dasharray: 200 10;
			}
			&__circle4 {
				display: none;
				stroke-width: 2;
				stroke-dasharray: 0 200;
				transition: all 1.5s ease;
				stroke-dashoffset: 190px;
			}
			&:hover {
				text-decoration: none;
				color: $orange;

			}
			&:hover .logo__circle2,
			&:hover .logo__circle4 {
				stroke-dasharray: 500 0;
			}
		}

		.burger {
			right: 0;
			display: none;
			flex-direction: column;
			justify-content: space-between;
			width: 36px;
			padding: 5px;
			height: 26px;
			cursor: pointer;
			position: relative;
			transition: 1.1s 0.5s;

			&:hover .burger__line-one:before,
			&:hover .burger__line-three:before,
			&:hover .burger__line-two:before {
				width: 26px;
			}

			&__line-one,
			&__line-three,
			&__line-two {
				background: $white;
				height: 3px;
				width: 26px;

				&:before {
					display: block;
					content: '';
					background: $orange;
					height: 3px;
					width: 0;
				}
			}

			&__line-one {
				transition: 1.1s 0.5s;

				&:before {
					transition: all 0.3s ease-out;
				}
			}

			&__line-two {
				transform: rotate(180deg);
				transition: 1.1s 0.5s;

				&:before {
					transition: all 0.5s ease-out;
				}
			}

			&__line-three {
				transition: 0.7s 0.6s;

				&:before {
					transition: all 0.9s ease-out;
				}
			}
		}

		.burger-close {
			z-index: 1000;
			position: fixed!important;
			right: 10%;
			top: 20px;
			transition: 1.1s 0.5s;
			width: 40px;
			height: 40px;

			.burger__line-one {
				background: #737373;
				transform: rotate(45deg) translate(10px, 10px);
			}

			.burger__line-two {
				opacity: 0;
			}

			.burger__line-three {
				background: #737373;
				transform: rotate(-405deg) translate(9px, -9px);
			}
		}
	}

	.nav {
		&__item {
			display: flex;

			&:before {
				display: block;
				content: '\\\\';
				margin-right: 35px;
				margin-left: 35px;
				font-size: 18px;
			}

			&:first-child:before {
				content: '';
				margin-right: 0;
				margin-left: 0;
			}
		}

		&__elem {
			color: $white;
			text-transform: uppercase;
			font-size: 18px;
			font-weight: 400;
			transition: $transition05;

			&:hover {
				text-decoration: none;
				color: $black;
			}
		}
	}

	.nav-mobile {
		display: none;
		position: fixed;
		background: #f0f0f0;
		z-index: 999;
		opacity: 0;
		transform: scale(0);
		top: 0;
		right: 0;
		transition: 0.55s 0.5s;
		z-index: 999;
		width: 100%;
		height: 100vh;

		&__list {
			height: 100vh;
			justify-content: space-evenly;
		}

		&__item {
			display: flex;
		}

		&__elem {
			color: #737373;
			font-size: 23px;
			padding: 10px;
			display: block;
			font-weight: 600;
			transition: $transition05;

			&:hover {
				text-decoration: none;
				color: $orange;
			}
		}
	}

	.nav-mobile-active {
		opacity: 1;
		transform: scale(1);
	}

	&__circle {
		display: none;
		width: 200px;
		height: 200px;
		background: #f0f0f0;
		position: fixed;
		z-index: 1000;
		top: 50%;
		left: 50%;
		transition: 0.5s 0.6s;
		opacity: 0;
		transform: translate(-50%, -50%) scale(0);
		border-radius: 100px;
	}

	&__circle-active {
		transition: 0.5s 0.3s;
		transform: translate(-50%, -50%) scale(3);
		opacity: 1;
		z-index: 998;
	}

	&__subtitle {
		font-size: 45px;
		font-weight: 300;
		text-transform: uppercase;
		letter-spacing: -0.02em;
	}

	&__title {
		font-size: 70px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: -0.06em;
	}

	&__button {
		display: block;
		font-size: 15px;
		font-weight: 400;
		text-transform: uppercase;
		color: $white;
		padding: 10px 0;
		border: 2px solid $white;
		margin: 90px auto 130px;
		width: 160px;
		transition: $transition05;
		outline: 0;

		&:hover {
			color: $white;
			text-decoration: none;
			background: $orange;
			border-color: $orange;
		}

		&:active {
			box-shadow: 0 0 10px $orange;
		}
	}

	.slick-dots {
		display: flex;
		justify-content: center;

		.slick-active .header__dots {
			background: $white;
		}
	}

	&__dots {
		width: 12px;
		height: 12px;
		background: transparentize($white, .6);
		border-radius: 100px;
		margin: 3px;
		cursor: pointer;
		transition: $transition05;

		&:hover {
			background: transparentize($white, .4);
		}
	}
}

.about {
	padding: 100px 0;

	.story {
		padding-top: 145px;
		border-bottom: 2px solid #e5e5e5;
		padding-bottom: 125px;

		&__icons {
