			}
		}
	}

	.skills {
		padding-top: 120px;

		&__image {
			width: 50%;
			position: relative;
			padding-right: 80px;

			img {
				max-height: 100%;
				max-width: 100%;
			}
		}

		&__picture {
			position: absolute;
			display: block;
			bottom: -5px;
			left: 0;
			margin-left: 20px;
		}

		&__wrap {
			width: 50%;
		}

		&__icons {
			width: 125px;
			height: 125px;

			img {
				height: 50px;
			}
		}

		&__text {
			width: 405px;
		}

		&__title {
			margin-bottom: 22px;
		}

		&__list {
			margin-top: 55px;
		}

		&__item {
			width: 100%;
			margin-top: 40px;
		}

		&__name {
			font-weight: 600;
			text-transform: uppercase;
			margin-bottom: 10px;

			span {
				font-weight: 300;
			}
		}

		&__graph {
			width: 100%;
			height: 10px;
			background: #e5e5e5;
		}

		&__line {
			height: 10px;
			background: $orange;
		}

		&__item:nth-child(1) .skills__line {
			width: 88%;
		}

		&__item:nth-child(2) .skills__line {
			width: 76%;
		}

		&__item:nth-child(3) .skills__line {
			width: 81%;
		}

		&__item:nth-child(4) .skills__line {
			width: 71%;
		}
	}
}

.minib {
	background: url("../img/minib-bg.jpg") no-repeat center center/cover;
	min-height: 700px;
	color: $white;
	padding-top: 250px;
	padding-bottom: 250px;
	position: relative;
	width: 100%;
	overflow: hidden;

	&__triangle {
		&_top {

			left: 0;
			bottom: 0;
			border-bottom: 50px solid $white;
			border-left: 100vw solid transparent;
			position: absolute;
		}

		&_bottom {
			right: 0;
			top: 0;
			border-top: 50px solid $white;
			border-right: 100vw solid transparent;
			position: absolute;
		}
	}

	&__citation {
		line-height: 1.5em;

		&:after {
			font-size: 110px;
			font-weight: 700;
			display: block;
			content: '\201D';
			line-height: 1em;
		}

		&:before {
			font-size: 110px;
			font-weight: 700;
			display: block;
			content: '\201C';
			line-height: 1em;
		}
	}

	&__author {
		font-weight: 700;
		font-size: 24px;
		text-transform: uppercase;
	}

	&__item {
		&:hover {
			cursor: pointer;
		}

		&:hover .minib__icons {
			color: $orange;
			border-color: #fff;
		}
	}

	&__icons {
		transition: $transition05;
		width: 170px;
		height: 170px;
		font-size: 32px;
		margin-bottom: 30px;
	}

	&__title {}

	&__subtitle {
		font-weight: 700;
		font-size: 25px;
	}

	&__quote {
		max-width: 800px;
		font-size: 22px;
		font-weight: 100;
		font-style: italic;
		margin: 0 auto;
	}

	&__image {
		width: 105px;
		height: 105px;
		flex-shrink: 0;
		border-radius: 100px;
		border: 6px solid #f2f2f2;
		position: relative;
		box-shadow: 0 0 0 1px #e5e5e5;
		overflow: hidden;
		margin: 25px auto;

		img {
			min-width: 105px;
			min-height: 105px;
			width: 100%;
			object-fit: cover;
			image-rendering: crisp-edges;
		}
	}

	.slick-dots {
		display: flex;
		justify-content: center;

		.slick-active .minib__dots {
			background: $white;
		}
	}

	&__dots {
		width: 12px;
		height: 12px;
		background: transparentize($white, .6);
		border-radius: 100px;
		margin: 3px;
		cursor: pointer;
		transition: $transition05;

		&:hover {
			background: transparentize($white, .4);
		}
	}

	&__comment {
		width: 105px;
		height: 105px;
		border-color: $white;
		font-size: 30px;
		color: $orange;
		margin: 0 auto;
	}

	&__text {
		font-size: 22px;
		max-width: 835px;
		font-weight: 600;
		margin: 30px auto 20px;

		a {
			color: $orange;
		}
	}

	&__mark {
		color: #808080;
		font-size: 15px;
		font-style: italic;
	}

	&__clients {
		margin-top: 50px;
	}

	&__cell {
		max-width: 260px;
		width: 25%;
		cursor: pointer;

		img {
			width: 100%;
			filter: grayscale(1);
			transition: $transition05;
			image-rendering: -webkit-optimize-contrast;

			&:hover {
				filter: none;
			}
		}
	}
}

.team {
	padding: 100px 0;

	&__list {
		padding-top: 120px!important;
	}

	&__item {
		width: 24%;
		padding: 30px 15px;
		border-top: 4px solid transparent;
		transition: $transition05;
		cursor: pointer;

		&:hover {
			background: #f0f0f0;
			border-top-color: #f39c12;
		}
	}

	&__image {
		width: 200px;
		height: 200px;
		flex-shrink: 0;
		border-radius: 100px;
		border: 6px solid #f2f2f2;
		position: relative;
		box-shadow: 0 0 0 1px #e5e5e5;
		overflow: hidden;
		margin-bottom: 25px;

		img {
			min-width: 200px;
			min-height: 200px;
			width: 100%;
			object-fit: cover;
			image-rendering: crisp-edges;
		}
	}

	&__profession {
		font-family: 'Lato', sans-serif;
		font-size: 15px;
		color: #999999;
		font-weight: 400;

		&:after {
			display: block;
			content: '';
			height: 4px;
			width: 20px;
			background: #cccccc;
			margin: 10px auto;
		}
	}

	&__soc {
		margin-top: 20px;
	}

	&__elem {
		font-size: 18px;
		color: $silver;
		margin: 0 4px;
		transition: $transition05;

		&:hover {
			text-decoration: none;
		}

		&_twitter {
			&:hover {
				color: #1da1f2;
			}
		}

		&_facebook {
			&:hover {
				color: #3b5998;
			}
		}

		&_basketball {
			&:hover {
				color: #ea4c89;
			}
		}

		&_google {
			&:hover {
				color: #db4a39;
			}
		}

		&_linkedin {
			&:hover {
				color: #2867B2;
			}
		}

		&_instagram {
			&:hover {
				color: #dc2743;
				background: repeating-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
		}
	}
}

.service {
	padding: 100px 0;

	&__head {
		margin-bottom: 180px;
	}

	.activities {
		margin: 125px 0 !important;
		padding-bottom: 125px;
		border-bottom: 2px solid #e5e5e5;

		&__item {
			display: flex;
			align-items: center;
